import React, { Component } from 'react';
import Country from './components/Country';
import './App.css';
import { Avatar, Grid } from '@mui/material';
import './App.css';
import { Container } from '@mui/system';

class App extends Component {
  state = { 
    countries: [
      { id: 1, name: 'United States', gold: 2, silver: 4, bronze: 4},
      { id: 2, name: 'China', gold: 3, silver: 2, bronze: 2},
      { id: 3, name: 'Germany', gold: 0, silver: 1, bronze: 5},
      { id: 4, name: 'Japan', gold: 1, silver: 5, bronze: 1},
      { id: 5, name: 'Sweeden', gold: 6, silver: 0, bronze: 0}
    ],
    medals: [
      { id: 1, name: 'gold', color: '#FFD700' },
      { id: 2, name: 'silver', color: '#C0C0C0' },
      { id: 3, name: 'bronze', color: '#CD7F32' }
    ]
   }

   //methods
   handleIncrement = (countryId, medal) => {
    const mutableCountries = [...this.state.countries];
    const index = mutableCountries.findIndex((c) => c.id === countryId);
    mutableCountries[index][medal] += 1;
    this.setState({ countries: mutableCountries });
   };
  
   handleDecrement = (countryId, medal) => {
    const mutableCountries = [...this.state.countries];
    const index = mutableCountries.findIndex((c) => c.id === countryId);
    if (mutableCountries[index][medal] > 0) {
          mutableCountries[index][medal] -= 1;
    }
    this.setState({ countries: mutableCountries });
   };

   getTotalMedals() {
    let total = 0;
    this.state.medals.forEach(medal => { total += this.state.countries.reduce((a, b) => a + b[medal.name], 0) });
    return total;
   }


  render() { 
    return (
        <Container maxWidth='false' className='App'>
          <div className='Page-Title'>
            <h1 className='Page-Header'>Olympic Medals</h1>
            <Avatar sx={{ bgcolor: '#60F',  marginTop: '30px', marginLeft: '30px' }}>{ this.getTotalMedals() }</Avatar>
          </div>
          <Grid container spacing={2} direction="row" alignItems="flex-start" justify="flex-start">
              { this.state.countries.map(country => 
                <Country 
                  key={ country.id }
                  country = { country }
                  medals = { this.state.medals }
                  onIncrement = { this.handleIncrement }
                  onDecrement = { this.handleDecrement }
                  />
                )}
          </Grid>
        </Container>
    );
  }
}
 
export default App;