import React, { Component } from 'react';
import { CardContent, Grid, Card, Divider, Avatar, Typography } from '@mui/material';
import Medal from './Medal';

class Country extends Component {

    //methods
    getTotalMedals() {
        let total = 0;
        this.props.medals.forEach(medal => {
            total += this.props.country[medal.name];
        });
        return total;
    }

    render() { 
        const { onIncrement, onDecrement, country, medals } = this.props;
        return (
            <Grid item xs={12} sm={6} md={3}>
                <Card className='Country-Card' elevation={6} sx={{  bgcolor: '#FFF' }}>
                    <CardContent>
                        <div className='Title-Container'>
                            <Typography gutterBottom variant='h3' component='div'>{ country.name }</Typography>
                            <Avatar alt='total medal count' sx={{ bgcolor: '#60F', marginTop: '10px' }}>{ this.getTotalMedals() }</Avatar>
                        </div>                            
                        <Divider variant="fullWidth" />
                        { medals.map(medal =>
                            <Medal
                                key={ medal.id }
                                color= { medal.color }
                                name={ medal.name }
                                country={ country }
                                onIncrement= { onIncrement }
                                onDecrement= { onDecrement }
                            />
                            )}
                    </CardContent>
                </Card>
            </Grid>
        );
    }
}
 
export default Country;