import React, { Component } from 'react';
import { IconButton, Avatar, CardHeader } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

class Medal extends Component {
    render() { 
        const { onIncrement, onDecrement, country, name, color } = this.props;
        return (
            <CardHeader avatar={<Avatar alt={ name + ' medal count' } sx={{ bgcolor: color }}>{ country[name] }</Avatar>} 
                        title={ name + ' medals' } titleTypographyProps={{variant:'h5' }}
                        action={<div><IconButton color="error" onClick={ () => onDecrement(country.id, name) } aria-label='decrease medal count'>
                            <RemoveIcon />
                            </IconButton>
                            <IconButton color="primary" onClick={ () => onIncrement(country.id, name) } aria-label='increase medal count'>
                                <AddIcon />
                            </IconButton></div>}
            />
        );
    }
}
 
export default Medal;
